html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  --light-red: hsl(0, 100%, 67%);
  --orange-yellow: hsl(39, 100%, 56%);
  --green-teal: hsl(166, 100%, 37%);
  --cobalt-blue: hsl(234, 85%, 45%);
  --reaction-bg: hsl(0, 32%, 93%);
  --memory-bg: hsl(47, 23%, 92%);
  --verbal-bg: hsl(164, 39%, 89%);
  --visual-bg: hsl(240, 58%, 91%);
  --card1-bg: linear-gradient(
      to bottom,
      hsl(252, 100%, 67%) 1%,
      hsl(241, 81%, 54%) 100%
    )
    fixed;
  --circle-bg: linear-gradient(
      to bottom,
      hsla(256, 72%, 46%, 1) 30%,
      hsla(241, 72%, 46%, 0) 100%
    )
    fixed;
  --text-color1: hsl(0, 0%, 100%);
  --text-color2: hsl(0, 0%, 96.5%);
  --text-color3: hsl(241, 100%, 89%);
  --button-color: hsl(224, 30%, 27%);
}

:root {
  --font-family: "Hanken Grotesk", sans-serif;
}

.container {
  padding: 0;
  max-width: 69.375rem;
  margin: 0;
  display: flex;
  flex-direction: column;
  background: var(--text-color2);
}
@media (min-width: 36em) {
  .container {
    display: grid;
    align-self: center;
    justify-self: center;
    grid-template-columns: repeat(2, 1fr);
    width: 31.25rem;
    border-radius: 1.5625rem;
    box-shadow: 0.09375rem 0.09375rem 0.25rem hsl(240, 30%, 85%);
    overflow: auto;
  }
}

h1,
h2,
p {
  margin: 0;
}

h1 {
  font-size: 1.125rem;
  font-weight: 700;
}

h2 {
  font-size: 0.875rem;
}

body {
  font-family: var(--font-family);
}
@media (min-width: 36em) {
  body {
    display: grid;
    place-items: center;
    width: 95vw;
    height: 95vh;
  }
}

p {
  font-size: 0.78125rem;
}

.card1 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1.5fr 4fr 1.5fr 2fr;
  align-items: center;
  text-align: center;
  background: var(--card1-bg);
  border-radius: 0 0 1.5625rem 1.5625rem;
  height: 45%;
  margin: 0;
  padding: 0 3.75rem;
}
@media (min-width: 36em) {
  .card1 {
    height: 100%;
    border-radius: 1.5625rem;
    padding: 0 2.1875rem;
  }
}
.card1__result, .card1__total {
  color: var(--text-color3);
}
.card1__score {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  justify-self: center;
  background: var(--circle-bg);
  border-radius: 50%;
  width: 6.875rem;
  height: 6.875rem;
}
@media (min-width: 36em) {
  .card1__score {
    width: 9.375rem;
    height: 9.375rem;
    transform: translateY(-0.625rem);
  }
}
.card1__number {
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-color1);
}
.card1__remark {
  color: var(--text-color1);
  margin-top: 0.625rem;
}
@media (min-width: 36em) {
  .card1__remark {
    transform: translateY(-1.25rem);
  }
}
.card1__comment {
  color: var(--text-color2);
  opacity: 0.8;
  margin-top: -1.875rem;
}
@media (min-width: 36em) {
  .card1__comment {
    transform: translateY(-0.9375rem);
  }
}

.main-card {
  background: var(--text-color1);
}
@media (min-width: 36em) {
  .main-card {
    padding: 0.5625rem 0.8125rem 0;
  }
}

.title {
  margin: 0.7rem 1.25rem 0.9375rem;
}
@media (min-width: 36em) {
  .title {
    margin-left: 0.3125rem;
    margin-right: 0.3125rem;
  }
}

.card-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  gap: 0.8125rem;
}

.card2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.3125rem;
  padding: 0.4375rem;
  margin: 0 1.25rem;
  height: 2.5rem;
}
@media (min-width: 36em) {
  .card2 {
    margin-left: 0.3125rem;
    margin-right: 0.3125rem;
  }
}
.card2__reaction {
  background-color: var(--reaction-bg);
}
.card2__memory {
  background-color: var(--memory-bg);
}
.card2__verbal {
  background-color: var(--verbal-bg);
}
.card2__visual {
  background-color: var(--visual-bg);
}
.card2__test {
  display: flex;
  align-items: center;
}
.card2__number {
  display: inline-block;
  margin-left: 0.375rem;
}
.card2__number--reaction {
  color: var(--light-red);
}
.card2__number--memory {
  color: var(--orange-yellow);
}
.card2__number--verbal {
  color: var(--green-teal);
}
.card2__number--visual {
  color: var(--cobalt-blue);
}
.card2__score {
  opacity: 0.5;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.875rem;
  margin: 1.875rem 1.25rem;
  height: 2.8125rem;
  border: none;
  border-radius: 1.5625rem;
}
@media (min-width: 36em) {
  .button {
    margin-left: 0.3125rem;
    margin-right: 0.3125rem;
  }
}

input {
  border: none;
  border-radius: 1.5625rem;
  background: var(--button-color);
  font-weight: 700;
  color: var(--text-color1);
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

input:hover {
  cursor: pointer;
  background: var(--card1-bg);
}